<template>
  <v-app>
    <div id="app" class="cute">
      <GameHeader />
      <div class="main">
        <router-view />
      </div>
      <GameFooter />
    </div>
  </v-app>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameFooter from './views/footer/GameFooter.vue'
import GameHeader from './views/GameHeader.vue'
import WebStorageService from '@/services/web-storage.service'
import CommonService from '@/services/common.service'

export default {
  name: 'App',
  components: {
    GameFooter,
    GameHeader
  },
  computed: mapState({
    defaultSettings: state => state.game.settings,
    isMobile: state => state.settings.isMobile
  }),
  data: () => ({
    webStorageService: new WebStorageService(),
    commonService: new CommonService()
  }),
  created () {
    this.$nextTick(function () {
      window.addEventListener('resize', this.setMobile)
      this.setMobile()
    })
    let settings = this.webStorageService.getLocalStorage('settings')
    if (settings) {
      settings = { ...this.defaultSettings, ...settings }
      this.changeSettings(settings)
    }
    const lang = this.webStorageService.getLocalStorage('lang') || 'en'
    this.commonService.changeLang(lang)
  },
  methods: {
    setMobile (event) {
      const isMobile = document.documentElement.clientWidth < 768
      if (isMobile !== this.isMobile) {
        this.changeIsMobile(isMobile)
      }
    },
    ...mapActions('settings', ['changeIsMobile']),
    ...mapActions('game', ['changeSettings'])
  }
}
</script>

<style lang="scss">
#app {
  height: 100%;
  position: fixed;
}
.main {
  padding: 60px 0 72px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("./assets/images/background.svg");
  background-size: cover;
  background-position: right 40% top 50px;
  height: 100%;
}
</style>
