<template>
  <div class="gamePlayers_container" :class="{'gamePlayers_container-active' : game.status > 0}">
    <div class="gamePlayers">
      <div
        v-for="(player) in game.players"
        class="gamePlayers_player"
        :key="player.id"
        :ref="'player' + player.id"
      >
        <div
          class="gamePlayers_player_color"
          :class="[
            {'gamePlayers_player_color-active' : player.id === playerId},
            {'gamePlayers_player_color-isPlayer' : player.isPlayer},
            'gamePlayers_player_color-' + player.color]"
        >
          {{player.size}}
        </div>
        <div v-if="player.reserve" class="gamePlayers_player_reserve">
          <span class="gamePlayers_player_reserve_icon icon icon-treasure"></span>
          {{player.reserve}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'GamePlayers',
  computed: mapState({
    game: state => state.game,
    playerId: state => state.game.player.id
  }),
  data: () => ({
    newGameDialog: false
  }),
  methods: {},
  watch: {
    playerId (val) {
      const el = this.$refs['player' + (val + 1)]
      if (el && el[0]) {
        el[0].scrollIntoView({
          behavior: 'smooth',
          block: 'center',
          inline: 'center'
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.gamePlayers {
  height: 70px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  scroll-snap-type: x mandatory;
  box-sizing: border-box;
  white-space: nowrap;
  text-align: center;
  width: 100%;
  padding: 0 10px;
  &_container {
    width: 100vw;
    position: relative;
    opacity: 0;
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    &-active {
      opacity: 1;
    }
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      width: 20px;
      z-index: 1;
    }
    &:before {
      left: 0;
      background-image: linear-gradient(
        to right,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0)
      );
    }
    &:after {
      right: 0;
      background-image: linear-gradient(
        to left,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 0)
      );
    }
  }
  &_player {
    display: inline-flex;
    align-items: center;
    margin: 0 10px;
    position: relative;
    scroll-snap-align: center;
    flex-direction: column;
    justify-content: center;
    width: 40px;
    &_color {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      position: relative;
      font-size: 30px;
      line-height: 36px;
      text-align: center;
      box-sizing: border-box;
      color: var(--primary-color);
      transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      position: relative;
      background-color: #fff;
      border: 3px solid transparent;
      &-active {
        color: #fff;
      }
      &-isPlayer {
        border-radius: 5px;
      }
      span {
        position: relative;
        z-index: 1;
      }
    }
    &_reserve {
      margin-top: 5px;
      display: flex;
      align-items: center;
      line-height: 18px;
      &_icon {
        margin-right: 4px;
      }
    }
  }
  @each $i, $color in $colors {
    &_player {
      &_color {
        &-#{$i}{
          border-color: var(--player-color-#{$i});
          &.gamePlayers_player_color-active {
          background-color: var(--player-color-#{$i});
          }
        }
      }
    }
  }
}
</style>
