<template>
  <div class="gameIntro_container" :class="{'gameIntro_container-active' : status === 0}">
    <div class="gameIntro">
      <div class="gameIntro_slides_container">
        <div
          class="gameIntro_slides"
          :class="[{'gameIntro_slides-end': end}, 'gameIntro_slides-' + step]"
        >
          <div
            v-for="(step, i) in 5"
            class="gameIntro_slide"
            :class="['gameIntro_slide-' + i]"
            :key="step"
          >
            <div class="gameIntro_text">{{i18n.intros[i]}}</div>
          </div>
        </div>
      </div>
      <div class="gameIntro_indicator_container">
        <div class="gameIntro_indicator" :class="['gameIntro_indicator-' + step]">
          <span
            class="gameIntro_indicator_icon gameIntro_indicator_icon icon icon-chevron"
            :class="{'gameIntro_indicator_icon-animate' : start}"
          ></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: 'GameIntro',
  computed: mapState({
    status: state => state.game.status,
    step: state => state.settings.introStep,
    i18n: state => state.i18n
  }),
  data: () => ({
    stepper: null,
    start: false,
    end: false
  }),
  mounted () {
    this.start = true
    this.toStep()
  },
  destroyed () {
    clearTimeout(this.stepper)
  },
  methods: {
    toStep () {
      this.end = false
      this.changeIntroStep((this.step + 1) % 5)
      if (this.step === 4) {
        this.stepper = setTimeout(() => {
          this.end = true
        }, 1000)
      }
      this.stepper = setTimeout(() => {
        this.toStep()
      }, 4000)
    },
    ...mapActions('settings', ['changeIntroStep'])
  },
  watch: {
    status (val) {
      if (val > 0) {
        clearTimeout(this.stepper)
        this.changeIntroStep(-1)
      }
    }
  }
}
</script>

<style scoped lang="scss">
@keyframes indicator {
  33% {
    transform: scale(1, 1) translateY(0);
  }
  66% {
    transform: scale(1.1, 1.1) translateY(4px);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}

.gameIntro {
  &_container {
    position: absolute;
    bottom: 72px;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    &-active {
      opacity: 1;
    }
  }
  &_slides {
    &_container {
      margin: 0 20px;
      background-color: rgba(255, 255, 255, 0.5);
      border-radius: 8px;
      padding: 8px;
      overflow: hidden;
    }
    white-space: nowrap;
    transition: transform 0.5s cubic-bezier(0.4, 0, 0.6, 1);
    @for $i from 0 through 4 {
      &-#{$i} {
        transform: translateX($i * -100%);
        .gameIntro_slide-#{$i} {
          opacity: 1;
        }
      }
    }
    &-end {
      transition: none;
      transform: translateX(-100%);
    }
    &-end,
    &-0 {
      .gameIntro_slide-1,
      .gameIntro_slide-2,
      .gameIntro_slide-3 {
        display: none;
      }
    }
  }
  &_slide {
    display: inline-block;
    width: 100%;
    text-align: center;
    opacity: 0;
    transition: all 0.5s cubic-bezier(0.4, 0, 0.6, 1);
  }
  &_indicator {
    min-width: 64px;
    width: 20vw;
    max-width: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.5s cubic-bezier(0.4, 0, 0.6, 1);
    @for $i from 0 through 4 {
      &-#{$i} {
        transform: translateX($i * 20vw);
      }
    }
    &_icon {
      color: #fff;
      font-size: 24px;
      &-animate {
        animation: indicator 2s infinite;
      }
    }
  }
}
</style>
