<template>
  <div class="gameFooterTimer" @click="next" v-ripple>
    <span class="gameFooterTimer_countdown">00:{{timer > 9 ? '' : '0'}}{{timer}}</span>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'

export default {
  name: 'GameFooterTimer',
  computed: mapState({
    game: state => state.game
  }),
  data: () => ({
    gameService: new GameService(),
    timer: 11,
    countDown: null,
    delay: 250
  }),
  mounted () {
    this.timer = this.game.settings.size * 2 + 1
    this.count()
  },
  methods: {
    count () {
      this.timer--
      this.countDown = setTimeout(() => {
        this.count()
      }, 1000)
    },
    next () {
      this.timer = 0
    },
    ...mapActions('game', ['changeBusy'])
  },
  watch: {
    timer (val) {
      if (this.timer < 1) {
        clearTimeout(this.countDown)
        if (this.game.busy) {
          this.delay = 1000
        }
        this.changeBusy(true)
        setTimeout(() => {
          this.gameService.nextPlayer()
        }, this.delay)
      }
    }
  }
}
</script>

<style lang="scss">
.gameFooterTimer {
  border-radius: 6px;
  &_countdown {
    font-size: 32px;
  }
}
</style>
