<template>
  <div
    class="tutorial_container"
    :class="{'tutorial_container-click' : tutorial.steps[settings.tutorialStep].delay}"
    @click="nextTutorial"
  >
    <div class="tutorial">
      <template v-for="(step, i) in tutorial.steps">
        <div
          v-if="!step.done"
          class="tutorial_text"
          :class="[
          {'tutorial_text-active' : i === settings.tutorialStep},
          {'tutorial_text-attack' : step.action === 'attack'},
          {'tutorial_text-noFade' : step.noFade},
          'tutorial_text-position-' + step.position,
          'tutorial_text-color-' + gameSettings.color
        ]"
          :key="i"
          v-html="i18n.tutorials[i]"
        ></div>
      </template>
      <div
        class="tutorial_highlight_container"
        :class="['tutorial_highlight_container-' + this.tutorial.steps[this.settings.tutorialStep].highlight]"
      >
        <div
          class="tutorial_highlight"
          :class="{'tutorial_highlight-active' : this.tutorial.steps[this.settings.tutorialStep].highlight}"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import TutorialService from '@/services/tutorial.service'
import { tutorial } from '../globals/globals'

export default {
  name: 'Tutorial',
  computed: mapState({
    gameSettings: state => state.game.settings,
    settings: state => state.settings,
    i18n: state => state.i18n
  }),
  data: () => ({
    tutorialService: new TutorialService(),
    tutorial
  }),
  methods: {
    nextTutorial () {
      this.tutorialService.nextTutorial()
    }
  }
}
</script>

<style lang="scss">
.tutorial {
  &_container {
    position: absolute;
    top: 0;
    width: 100%;
    &-click {
      z-index: 9;
    }
  }
  position: relative;
  margin: 0 auto;
  width: 404px;
  height: 404px;
  max-width: calc(100vw - 30px);
  max-height: calc(100vw - 30px);
  &_text {
    position: absolute;
    z-index: 9;
    background-color: #fff;
    border: 1px solid var(--primary-color);
    border-radius: 4px;
    padding: 4px 8px;
    font-size: 14px;
    pointer-events: none;
    opacity: 0;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2),
      0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    transition: opacity 0.5s cubic-bezier(0.4, 0, 0.6, 1);
    b {
      font-weight: 700;
      transition: opacity 0.5s cubic-bezier(0.4, 0, 0.6, 1);
    }
    &:before {
      content: "";
      position: absolute;
      width: 40px;
      height: 40px;
      background-size: contain;
    }
    &-noFade {
      &:before {
        opacity: 0;
        transition: opacity 0.5s 0.5s;
      }
    }
    &-active {
      opacity: 1;
      pointer-events: all;
      &.tutorial_text-noFade {
        transition: none;
        &:before {
          opacity: 1;
          transition: opacity 0.5s cubic-bezier(0.4, 0, 0.6, 1);
        }
      }
    }
    &-attack {
      border-color: var(--error-color);
    }
    &-position {
      &-center {
        top: 50%;
        left: 50%;
        text-align: center;
        transform: translate(-50%, -50%);
        font-size: 16px;
        padding: 12px;
        min-width: 200px;
      }
      &-player-0 {
        top: -23px;
        left: calc(50% - 70px);
        &:before {
          top: -14px;
          left: -39px;
          background-image: url("../assets/images/arrow-left-top.svg");
        }
      }
      &-player-2 {
        top: -23px;
        right: calc(50% - 9px);
        &:before {
          top: -14px;
          right: -39px;
          background-image: url("../assets/images/arrow-right-top.svg");
        }
      }
      &-5 {
        top: calc(37.5% + 44px);
        left: calc(37.5% + 23px);
        &:before {
          top: -24px;
          left: -41px;
          background-image: url("../assets/images/arrow-left-top-attack.svg");
        }
      }
      &-5b {
        top: calc(12.5% + 44px);
        left: calc(12.5% + 23px);
        &:before {
          top: -14px;
          left: -39px;
          background-image: url("../assets/images/arrow-left-top.svg");
        }
      }
      &-6 {
        top: calc(37.5% + 58px);
        left: calc(62.5% + 21px);
        min-width: 94px;
        &:before {
          top: -24px;
          left: -41px;
          background-image: url("../assets/images/arrow-left-top-attack.svg");
        }
      }
      &-6b,
      &-6c {
        bottom: calc(62.5% - 26px);
        right: calc(50% - 17px);
        min-width: 170px;
      }
      &-6b {
        &:before {
          top: -24px;
          right: -41px;
          background-image: url("../assets/images/arrow-right-top.svg");
        }
      }
      &-6c {
        &:before {
          bottom: -24px;
          right: -41px;
          background-image: url("../assets/images/arrow-right-bottom.svg");
        }
      }
      &-6d {
        top: calc(37.5% + 53px);
        right: calc(37.5% + 24px);
        &:before {
          top: -24px;
          right: -41px;
          background-image: url("../assets/images/arrow-right-top.svg");
        }
      }
      &-7 {
        bottom: calc(62.5% + 22px);
        right: 25%;
        &:before {
          bottom: -40px;
          right: -14px;
          background-image: url("../assets/images/arrow-bottom-right.svg");
        }
      }
      &-11 {
        bottom: calc(37.5% + 22px);
        right: 25%;
        &:before {
          bottom: -40px;
          right: -14px;
          background-image: url("../assets/images/arrow-bottom-right.svg");
        }
        &.tutorial_text-attack {
          &:before {
            background-image: url("../assets/images/arrow-bottom-right-attack.svg");
          }
        }
      }
      &-11b,
      &-15b {
        right: calc(12.5% + 23px);
        &:before {
          right: -40px;
        }
      }
      &-11b {
        top: calc(62.5% + 11px);
        &:before {
          top: -2px;
          right: -40px;
          background-image: url("../assets/images/arrow-right-top.svg");
        }
      }
      &-11c {
        top: calc(62.5% - 75px);
        right: calc(12.5% + 25px);
        &:before {
          bottom: -40px;
          right: -14px;
          background-image: url("../assets/images/arrow-bottom-right.svg");
        }
      }
      &-15b {
        top: calc(87.5% - 77px);
        &:before {
          top: 24px;
          background-image: url("../assets/images/arrow-right-bottom.svg");
        }
      }
      &-15 {
        top: calc(100% + 4px);
        right: calc(12.5% + 25px);
        &:before {
          top: -24px;
          right: -40px;
          background-image: url("../assets/images/arrow-right-top.svg");
        }
      }
      &-results {
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
        min-width: 168px;
        text-align: center;
      }
    }
    &-color {
      @each $i, $color in $colors {
        &-#{$i} {
          .tutorial_text_color {
            background-color: var(--player-color-#{$i});
          }
        }
      }
    }
    &_color {
      height: 24px;
      width: 24px;
      border-radius: 4px;
      display: inline-flex;
      vertical-align: bottom;
      margin-left: 6px;
    }
  }
  &_highlight {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(
      90deg,
      transparent,
      hsla(0, 0%, 100%, 0.3),
      transparent
    );
    transform: translateX(-100%);
    &_container {
      position: absolute;
      z-index: 8;
      border-radius: 4px;
      overflow: hidden;
      pointer-events: none;
      &-1 {
        top: calc(25% + 3px);
        right: 4px;
        bottom: 4px;
        left: calc(75% + 1px);
        border-bottom-right-radius: 21px;
        .tutorial_highlight {
          animation: highlightSwipe 1s 0.5s infinite;
        }
      }
      &-2 {
        top: 4px;
        right: 4px;
        bottom: calc(75% + 1px);
        left: calc(50% + 2px);
        border-top-right-radius: 21px;
        .tutorial_highlight {
          animation: highlightSwipe 1s 0.5s infinite;
        }
      }
      &-3 {
        top: calc(50% + 2px);
        right: calc(25% + 3px);
        bottom: calc(25% + 3px);
        left: calc(50% + 2px);
        .tutorial_highlight {
          animation: highlightSwipe 1s 0.5s infinite;
        }
      }
    }
  }
}

@media (max-width: 410px) {
  .tutorial {
    width: 335px;
    height: 335px;
  }
}
@media (max-width: 374px) {
  .tutorial {
    width: 330px;
    height: 330px;
  }
}
@media (max-width: 359px) {
  .tutorial {
    width: 290px;
    height: 290px;
  }
}
</style>
