import WebStorageService from '@/services/web-storage.service'
import * as globals from '@/globals/globals'
import * as i18n from '@/globals/i18n'
import store from '@/store'

const webStorageService = new WebStorageService()
export default class CommonService {
  getName (arr, key, keyName = 'id', nameName = 'name') {
    let rtn = ''
    if (globals[arr]) {
      const node = globals[arr].filter(x => x[keyName] === key)[0]
      if (node) {
        rtn = (nameName === null ? node : node[nameName])
      }
    }
    return rtn
  }

  changeLang (lang) {
    store.dispatch('changeLang', i18n.languages[lang])
    webStorageService.setLocalStorage('lang', lang)
  }
}
