const state = {
  isMobile: false,
  showSettings: true,
  introStep: -1,
  tutorialStep: -1
}

// Getter functions
const getters = {
  getIsMobile () {
    return JSON.parse(JSON.stringify(state.isMobile))
  },
  getShowSettings () {
    return JSON.parse(JSON.stringify(state.showSettings))
  },
  getTutorialStep () {
    return JSON.parse(JSON.stringify(state.tutorialStep))
  },
  getIntroStep () {
    return JSON.parse(JSON.stringify(state.introStep))
  }
}

// Mutations
const mutations = {
  setIsMobile (state, data) {
    state.isMobile = data
  },
  setShowSettings (state, data) {
    state.showSettings = data
  },
  setTutorialStep (state, data) {
    state.tutorialStep = data
  },
  setIntroStep (state, data) {
    state.introStep = data
  }
}

// Actions
const actions = {
  changeIsMobile ({ commit }, data) {
    commit('setIsMobile', data)
  },
  changeShowSettings ({ commit }, data) {
    commit('setShowSettings', data)
  },
  changeTutorialStep ({ commit }, data) {
    commit('setTutorialStep', data)
  },
  changeIntroStep ({ commit }, data) {
    commit('setIntroStep', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
