<template>
  <div class="gameFooterSettings">
    <GameFooterColors
      :color.sync="settings.settings.color"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'color'}"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.size"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'size'}"
      type="size"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.players"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'players'}"
      type="players"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="settings.settings.mode"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'mode'}"
      type="mode"
      v-on:select="update"
    />
    <div class="gameFooterSettings_content_container">
      <div class="gameFooterSettings_content">
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link-color"
          :class="{'gameFooterSettings_color-inactive' : !settingsState.showSettings}"
          @click="toSelect('color')"
        >
          <div
            class="gameFooterSettings_color"
            :class="[
                  {'gameFooterSettings_color-intro' : settingsState.introStep === 0},
                  'gameFooterSettings_color-' + settings.settings.color
                ]"
          ></div>
        </v-btn>
        <div class="gameFooterSettings_content_right">
          <div
            class="gameFooterSettings_content_right_grid"
            :class="{'gameFooterSettings_content_right_grid-settings' : settingsState.showSettings}"
          >
            <div class="gameFooterSettings_content_controls">
              <template v-if="game.status < 2 && game.settings.mode !== 3">
                <div v-if="game.player.isPlayer">{{i18n.makeMove}}</div>
                <div v-else class="results_message">
                  <v-btn
                    color="primary"
                    class="results_message_btn"
                    large
                    @click="start"
                  >{{i18n.start}}</v-btn>
                </div>
              </template>
              <template v-else-if="game.status === 2">
                <template v-if="game.settings.mode === 3 && game.player.isPlayer">
                  <GameFooterTimer />
                </template>
                <v-btn
                  v-else-if="game.player.isPlayer"
                  color="primary"
                  :disabled="game.busy || game.settings.mode === 0"
                  @click="next"
                  large
                >{{i18n.next}}</v-btn>
              </template>
            </div>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-size"
              :class="{'gameFooterSettings_link-active' : select === 'size'}"
              @click="toSelect('size')"
            >
              <div
                class="gameFooterSettings_link_icon icon icon-board"
                :class="[
                  {'gameFooterSettings_link_icon-intro' : settingsState.introStep === 1},
                  'icon-board-' + settings.settings.size
                ]"
              ></div>
              <div class="gameFooterSettings_link_text">{{i18n['size_' + settings.settings.size]}}</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-players"
              :class="{'gameFooterSettings_link-active' : select === 'players'}"
              @click="toSelect('players')"
            >
              <div
                class="gameFooterSettings_link_icon gameFooterSettings_link_icon-players icon icon-players"
                :class="{'gameFooterSettings_link_icon-intro' : settingsState.introStep === 2}"
              >
                <span class="gameFooterSettings_link_icon_text">{{settings.settings.players}}</span>
              </div>
              <div class="gameFooterSettings_link_text">{{i18n.players}}</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-mode"
              :class="{'gameFooterSettings_link-active' : select === 'mode'}"
              @click="toSelect('mode')"
            >
              <div
                class="gameFooterSettings_link_icon icon icon-mode"
                :class="[
                  {'gameFooterSettings_link_icon-intro' : settingsState.introStep === 3},
                  'icon-mode-' + settings.settings.mode
                ]"
              ></div>
              <div class="gameFooterSettings_link_text">{{i18n['mode_' + settings.settings.mode]}}</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-play gameFooterSettings_link-active"
              @click="newGame()"
            >
              <div
                class="gameFooterSettings_link_icon icon icon-dices"
                :class="{'gameFooterSettings_link_icon-intro' : settingsState.introStep === 4}"
              ></div>
              <div class="gameFooterSettings_link_text">{{i18n.play}}</div>
            </v-btn>
            <v-btn
              text
              class="gameFooterSettings_link gameFooterSettings_link-settings"
              @click="toSettings"
            >
              <div class="gameFooterSettings_link_icon icon icon-settings"></div>
              <div class="gameFooterSettings_link_text">{{i18n.setup}}</div>
            </v-btn>
          </div>
        </div>
      </div>
    </div>
    <v-overlay
      :value="!!select || (game.status > 0 && settingsState.showSettings) || game.status > 3"
      :z-index="1"
      class="gameFooterSettings_overlay"
      @click.native="closeOverlay(null)"
    >
      <div
        class="gameFooterSettings_overlay_content"
        :class="{'gameFooterSettings_overlay_content-active' : !select}"
      >
        <template v-if="game.status === 4">
          <span v-html="i18n.lost"></span>
          <v-btn color="primary" class="gameFooterSettings_message_btn" large @click="newGame()">
            <span class="icon icon-swords"></span>
            {{i18n.revenge}}
          </v-btn>
        </template>
        <template v-else-if="game.status === 5">
          <span v-html="i18n.win"></span>
          <v-btn
            v-if="settingsState.tutorialStep > 0"
            color="primary"
            class="gameFooterSettings_message_btn"
            large
            @click="newGame(1)"
          >
            <span class="icon icon-tutorial"></span>
            {{i18n.tryMore}}
          </v-btn>
          <template v-else>
            <v-btn
              color="primary"
              class="gameFooterSettings_message_btn"
              large
              @click="newGame(game.settings.mode === 1 ? 2 : null)"
            >
              <span class="icon icon-crown"></span>
              {{game.settings.mode === 1 ? i18n.easy : i18n.more}}
            </v-btn>
            <v-btn
              v-if="game.settings.mode === 1"
              color="primary"
              outlined
              class="gameFooterSettings_message_btn gameFooterSettings_message_btn-last"
              large
              @click="newGame(game.settings.mode === 1)"
            >
              <span class="icon icon-bottle"></span>
              {{i18n.ok}}
            </v-btn>
          </template>
        </template>
        <span v-else class="gameFooterSettings_overlay_icon icon icon-pause"></span>
      </div>
    </v-overlay>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameFooterColors from './GameFooterColors.vue'
import GameFooterTimer from './GameFooterTimer.vue'
import GameFooterValue from './GameFooterValue.vue'

export default {
  name: 'GameFooterSettings',
  components: {
    GameFooterColors,
    GameFooterTimer,
    GameFooterValue
  },
  computed: mapState({
    game: state => state.game,
    settingsState: state => state.settings,
    gameResults: state => state.game.results,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    settings: { settings: {} },
    select: null,
    showSettings: true
  }),
  mounted () {
    this.$set(
      this.settings,
      'settings',
      JSON.parse(JSON.stringify(this.game.settings))
    )
  },
  methods: {
    toSelect (val) {
      this.select = this.select === val ? null : val
    },
    update () {
      setTimeout(
        function (scope) {
          scope.select = null
        },
        300,
        this
      )
    },
    newGame (mode = 0) {
      if (mode) {
        if (this.settingsState.tutorialStep > 0) {
          this.changeTutorialStep(-1)
        }
        this.$set(
          this.settings.settings,
          'mode',
          mode
        )
      }
      this.changeSettings(JSON.parse(JSON.stringify(this.settings.settings)))
      this.select = null
      this.changeShowSettings(false)
      this.gameService.newGame()
    },
    toSettings () {
      this.changeShowSettings(true)
    },
    start () {
      this.gameService.startGame()
    },
    next () {
      this.gameService.nextPlayer()
    },
    closeOverlay () {
      this.select = false
      if (this.game.status > 0) {
        this.changeShowSettings(false)
      }
    },
    ...mapActions('game', ['changeSettings']),
    ...mapActions('settings', ['changeShowSettings']),
    ...mapActions('settings', ['changeTutorialStep'])
  }
}
</script>

<style lang="scss">
@keyframes introColor {
  33% {
    transform: scale(1, 1);
  }
  66% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes introIcon {
  33% {
    transform: scale(1, 1);
  }
  66% {
    transform: scale(1.2, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}

.gameFooterSettings {
  &_select {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    height: 80px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    opacity: 0;
    &-active {
      opacity: 1;
      bottom: 72px;
      z-index: 3;
    }
  }
  &_content {
    &_container {
      width: 100%;
      background-color: #fff;
      position: relative;
      z-index: 2;
    }
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    max-width: 1024px;
    margin: 0 auto;
    height: 72px;
    align-items: center;
    justify-items: center;
    box-sizing: border-box;
    &_right {
      grid-column: 2 / span 4;
      width: 100%;
      height: 72px;
      overflow: hidden;
      &_grid {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        align-items: center;
        justify-items: center;
        grid-template-rows: 72px 72px;
        transform: translateY(0);
        transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
        &-settings {
          transform: translateY(-72px);
          .gameFooterSettings_link-size,
          .gameFooterSettings_link-players,
          .gameFooterSettings_link-mode,
          .gameFooterSettings_link-play {
            opacity: 0.65;
            &.gameFooterSettings_link-active {
              opacity: 1;
            }
          }
          .gameFooterSettings_content_controls,
          .gameFooterSettings_link-settings {
            opacity: 0;
          }
        }
      }
    }
    &_controls {
      grid-column: 1 / span 3;
      opacity: 1;
      width: 100%;
      text-align: center;
    }
  }
  &_link {
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    &.v-btn:not(.v-btn--round).v-size--default {
      min-width: 64px;
      width: 100%;
      max-width: 80px;
      height: 72px;
      font-weight: 400;
      padding: 0;
    }
    &-color {
      opacity: 1;
    }
    &-size {
      grid-column-start: 1;
      grid-row-start: 2;
    }
    &-players {
      grid-column-start: 2;
      grid-row-start: 2;
    }
    &-mode {
      grid-column-start: 3;
      grid-row-start: 2;
    }
    &-play {
      grid-column-start: 4;
      grid-row-start: 2;
    }
    &-settings {
      grid-column-start: 4;
      grid-row-start: 1;
      opacity: 1;
    }
    .v-btn__content {
      flex-wrap: wrap;
      color: var(--primary-color);
      width: 100%;
    }
    &_text {
      font-size: 14px;
      line-height: 14px;
      height: 14px;
      width: 100%;
      text-transform: none;
      white-space: normal;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &_icon {
      line-height: 34px;
      font-size: 30px;
      width: 100%;
      position: relative;
      color: var(--primary-color);
      &-intro {
        animation: introIcon 2s infinite;
      }
      &_text {
        font-family: "Comfortaa", Arial, Helvetica, sans-serif;
        font-size: 28px;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--primary-color);
      }
      &-players {
        color: #c4c4c4;
      }
    }
  }
  &_color {
    width: 48px;
    height: 48px;
    border-radius: 4px;
    transition: background-color 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    &-intro {
      animation: introColor 2s infinite;
    }
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: var(--player-color-#{$i});
      }
    }
    &-inactive {
      pointer-events: none;
    }
  }
  &_overlay {
    &_content {
      opacity: 0;
      color: rgba(255, 255, 255, 0.9);
      font-size: 64px;
      text-shadow: 2px 2px var(--text-color-dark);
      transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      text-align: center;
      display: flex;
      flex-direction: column;
      &-active {
        opacity: 1;
      }
    }
    &_icon {
      color: rgba(255, 255, 255, 0.65);
      font-size: 128px;
      text-shadow: none;
    }
  }
  &_message {
    &_btn {
      &-last {
        margin-top: 20px;
      }
      .icon {
        margin-right: 6px;
      }
    }
  }
}

.gameFooterSelect {
  padding: 0;
  margin: 0 auto;
  align-items: center;
  height: 80px;
  width: 100vw;
  text-align: center;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  &:after {
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 5px);
    bottom: -1px;
    height: 5px;
    width: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background-color: #fff;
  }
  &-color:after {
    left: calc(10% - 5px);
  }
  &-size:after {
    left: calc(30% - 5px);
  }
  &-mode:after {
    left: calc(70% - 5px);
  }
  //scroll-snap-type: x mandatory;
  &_padding {
    width: calc(50vw - 40px);
    height: 80px;
    flex-shrink: 0;
    display: inline-block;
    vertical-align: top;
    &-start {
      &-size {
        width: calc(30% - 40px);
      }
      &-mode {
        width: calc(70% - 40px);
      }
    }
    &-end {
      &-color {
        width: calc(100% - 80px);
      }
      &-size {
        width: calc(70% - 40px);
      }
      &-mode {
        width: calc(30% - 40px);
      }
    }
  }
  &_select {
    width: 80px;
    height: 80px;
    padding: 16px;
    text-align: center;
    line-height: 48px;
    font-size: 40px;
    color: #fff;
    //scroll-snap-align: center;
    display: inline-block;
    vertical-align: top;
    position: relative;
    &_content {
      width: 48px;
      height: 48px;
      transform-origin: center;
    }
    &_icon {
      position: relative;
      line-height: 48px;
    }
    &_text {
      position: absolute;
      line-height: 44px;
      font-size: 36px;
      color: var(--text-color-dark);
      display: block;
      text-align: center;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      font-family: "Comfortaa", Arial, Helvetica, sans-serif;
    }
    .icon-mode-1 {
      font-size: 35px;
    }
    .icon-mode-2 {
      font-size: 45px;
    }
  }
}
</style>
