<template>
  <div class="results_container">
    <div
      v-if="game.results && game.results.attackerDice && game.results.attacker === game.player.id"
      class="results"
    >
      <div
        class="results_side results_side-attacker"
        :class="{'results_side-win' : game.results.attackerDice.total > game.results.defenderDice.total}"
      >
        <div class="results_result results_result-attacker">
          <div
            class="results_dices"
            :class="['results_dices-' + game.results.attackerDice.dice.length]"
          >
            <div
              class="results_dice icon"
              :class="['icon-dice-' + dice]"
              v-for="(dice, i) in game.results.attackerDice.dice"
              :key="i"
            ></div>
          </div>
        </div>
        <div
          class="results_color"
          :class="[{'results_color-player' : game.results.attacker === game.settings.color}, 'results_color-' + game.results.attacker]"
        >{{game.results.attackerDice.total}}</div>
      </div>
      <div class="results_vs icon icon-swords"></div>
      <div
        class="results_side results_side-defender"
        :class="{'results_side-win' : game.results.attackerDice.total <= game.results.defenderDice.total}"
      >
        <div
          class="results_color"
          :class="[{'results_color-player' : game.results.defender === game.settings.color}, 'results_color-' + game.results.defender]"
        >{{game.results.defenderDice.total}}</div>
        <div class="results_result results_result-defender">
          <div
            class="results_dices"
            :class="['results_dices-' + game.results.defenderDice.dice.length]"
          >
            <div
              class="results_dice icon"
              :class="['icon-dice-' + dice]"
              v-for="(dice, i) in game.results.defenderDice.dice"
              :key="i"
            ></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'

export default {
  name: 'GameResults',
  computed: mapState({
    game: state => state.game,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    resultsActive: false
  }),
  methods: {},
  watch: {
    gameResults (val) {
      this.resultsActive = true
      const timeout = this.game.player.isPlayer ? 750 : 1500
      setTimeout(
        function (scope) {
          if (scope.gameResults === val) {
            scope.showResults = false
          }
        },
        timeout,
        this
      )
    }
  }
}
</script>

<style scoped lang="scss">
.results {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  &_container {
    height: 60px;
    display: flex;
    align-items: center;
  }
  &_side {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 130px;
  }
  &_color {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    z-index: 1;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 24px;
    box-sizing: border-box;
    &-player {
      border: 1px solid $color7;
    }
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: var(--player-color-#{$i});
      }
    }
  }
  &_result {
    display: flex;
    align-items: center;
    z-index: 1;
    width: 80px;
    &-attacker {
      justify-content: flex-end;
      margin-right: 10px;
      .results_dices {
        justify-content: flex-end;
      }
    }
    &-defender {
      justify-content: flex-start;
      margin-left: 10px;
      .results_dices {
        justify-content: flex-start;
      }
    }
  }
  &_dices {
    font-size: 13px;
    display: flex;
    flex-wrap: wrap;
    &-1 {
      max-width: 20px;
    }
    &-2,
    &-3,
    &-4 {
      max-width: 40px;
    }
    &-5,
    &-6 {
      max-width: 60px;
    }
    &-7,
    &-8 {
      max-width: 80px;
    }
  }
  &_dice {
    height: 20px;
    width: 20px;
    font-size: 20px;
    color: #fff;
  }
  &_vs {
    font-size: 24px;
    margin: 0 12px 0;
    color: #fff;
  }
}
</style>
