<template>
  <div class="gameFooter">
    <GameIntro />
    <GameResults />
    <div class="gameFooter_menu" :class="['gameFooter_menu-' + settings.color]">
      <GameFooterSettings />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameFooterSettings from './GameFooterSettings.vue'
import GameResults from './GameResults.vue'
import GameIntro from './GameIntro.vue'

export default {
  name: 'GameFooter',
  components: {
    GameResults,
    GameFooterSettings,
    GameIntro
  },
  computed: mapState({
    settings: state => state.game.settings
  })
}
</script>

<style lang="scss">
.gameFooter {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  &_menu {
    position: relative;
    border-top: 1px solid #c4c4c4;
    background-color: #fff;
    height: 72px;
    @each $i, $color in $colors {
      &-#{$i} {
        border-top-color: var(--player-color-#{$i});
      }
    }
  }
}
</style>
