<template>
  <div class="game" @click="deselect" v-touch:swipe.left="swipedLeft">
    <div class="game_start" v-html="i18n.tap"></div>
    <GamePlayers />
    <div
      class="game_board_container"
      :class="{'game_board_container-isPlayer' : game.player.isPlayer}"
    >
      <Tutorial v-if="game.settings.mode === 0 && game.status > 0" />
      <div
        class="game_board"
        :class="[
          {'game_board-busy' : game.busy},
          'game_board-size-' + game.settings.size,
          'game_board-player-' + game.settings.size,
          ]
        "
        @click="boardClick"
      >
        <div
          v-for="(cell, i) in game.board"
          :key="i"
          class="game_cell"
          :class="[
          {'game_cell-selectable' : selectable(i),
          'game_cell-selected' : i === game.selected,
          'game_cell-add' : cell.added,
          'game_cell-isPlayer' : cell.owner === game.player.id},
          'game_cell-owner-' + cell.owner,
          'game_cell-color-' + getColor(cell.owner),
          connectors(cell)
        ]"
          @click="select(i, cell.owner)"
          :depressed="i !== game.selected"
          v-ripple
        >
          <div class="game_cell_dices">
            <div class="game_cell_dices_left">
              <div v-for="(dice, i) in Math.min(cell.dice, 4)" :key="i" class="game_cell_dice"></div>
            </div>
            <div v-if="cell.dice > 4" class="game_cell_dices_right">
              <div v-for="(dice, i) in cell.dice - 4" :key="i" class="game_cell_dice"></div>
            </div>
          </div>
          <div v-if="!cell.top && !cell.left" class="game_cell_corner game_cell_corner-top-left"></div>
          <div v-if="!cell.top && !cell.right" class="game_cell_corner game_cell_corner-top-right"></div>
          <div
            v-if="!cell.bottom && !cell.right"
            class="game_cell_corner game_cell_corner-bottom-right"
          ></div>
          <div
            v-if="!cell.bottom && !cell.left"
            class="game_cell_corner game_cell_corner-bottom-left"
          ></div>
          <div
            class="game_cell_defender"
            :class="[{'game_cell_defender-active' : cell.defender}, 'game_cell_defender-' + cell.direction]"
          ></div>
          <!--<div
            class="game_cell_chain"
            :class="{'game_cell_chain-active' : cell.chain}"
          >{{cell.chain}}</div>-->
          <div class="game_cell_add">{{cell.added ? '+' + cell.added : ''}}</div>
        </div>
      </div>
      <div class="game_highlight_wrapper">
        <div
          class="game_highlight_container"
          :class="['game_highlight_container-size-' + game.settings.size]"
        >
          <div class="game_highlight"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import TutorialService from '@/services/tutorial.service'
import GamePlayers from './GamePlayers.vue'
import Tutorial from './Tutorial.vue'

export default {
  name: 'Game',
  components: {
    GamePlayers,
    Tutorial
  },
  computed: mapState({
    game: state => state.game,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    tutorialService: new TutorialService()
  }),
  methods: {
    select (i, owner) {
      if (i === this.game.selected) {
        this.deselect()
      } else if (owner === this.game.player.id) {
        this.gameService.changeSelected(i)
      } else {
        if (this.game.settings.mode !== 0) {
          this.gameService.attack(i)
        }
      }
      if (this.game.settings.mode === 0) {
        this.tutorialService.tutorialStep(i)
      }
    },
    selectable (i) {
      const cell = this.game.board[i]
      if (cell.owner === this.game.player.id) {
        if (cell.dice > 1) {
          const attackables = cell.adjacents.filter(cellId => {
            return this.game.board[cellId].owner !== cell.owner
          })
          return attackables.length > 0
        }
      } else if (this.game.selected !== null) {
        if (i === this.game.selected) {
          return true
        }
        if (this.game.board[this.game.selected].adjacents.includes(i)) {
          return true
        }
      }
      return false
    },
    boardClick (e) {
      e.stopPropagation()
      if (this.game.status < 2 && !this.game.player.isPlayer) {
        this.gameService.startGame()
      }
    },
    deselect () {
      if (this.game.board.length < 1) {
        this.gameService.newGame()
        this.changeShowSettings(false)
      } else if (!this.game.busy) {
        this.changeSelected(null)
      }
    },
    getColor (playerId) {
      return this.game.players.filter(player => {
        return player.id === playerId
      })[0].color
    },
    connectors (cell) {
      const connectors = []
      const flags = [false, false, false, false]
      const i = cell.id
      const owner = cell.owner
      // check top
      if (!cell.top) {
        const topId = i - this.game.settings.size
        if (this.game.board[topId].owner === owner) {
          connectors.push('game_cell-connectTop')
          flags[0] = true
        }
      }
      // check right
      if (!cell.right) {
        const rightId = i + 1
        if (this.game.board[rightId].owner === owner) {
          connectors.push('game_cell-connectRight')
          flags[1] = true
        }
      }
      // check bottom
      if (!cell.bottom) {
        const bottomId = i + this.game.settings.size
        if (this.game.board[bottomId].owner === owner) {
          connectors.push('game_cell-connectBottom')
          flags[2] = true
        }
      }
      // check left
      if (!cell.left) {
        const leftId = i - 1
        if (this.game.board[leftId].owner === owner) {
          connectors.push('game_cell-connectLeft')
          flags[3] = true
        }
      }
      // set rounded inner corners
      if (flags[0]) {
        if (flags[3]) {
          connectors.push('game_cell-cornerTopLeft')
        }
        if (flags[1]) {
          connectors.push('game_cell-cornerTopRight')
        }
      }
      if (flags[2]) {
        if (flags[1]) {
          connectors.push('game_cell-cornerBottomRight')
        }
        if (flags[3]) {
          connectors.push('game_cell-cornerBottomLeft')
        }
      }
      // set rounded outer corners
      if (!cell.top && !flags[0]) {
        if (!cell.right && !flags[1]) {
          connectors.push('game_cell-connectTopRight')
        }
        if (!cell.left && !flags[3]) {
          connectors.push('game_cell-connectTopLeft')
        }
      }
      if (!cell.bottom && !flags[2]) {
        if (!cell.right && !flags[1]) {
          connectors.push('game_cell-connectBottomRight')
        }
        if (!cell.left && !flags[3]) {
          connectors.push('game_cell-connectBottomLeft')
        }
      }
      return connectors
    },
    swipedLeft () {
      /* if (this.game.status === 2 && this.game.player.isPlayer) {
        this.gameService.nextPlayer()
      } */
    },
    ...mapActions('game', ['changeSelected']),
    ...mapActions('settings', ['changeShowSettings'])
  }
}
</script>

<style lang="scss">
@keyframes add {
  0% {
    opacity: 0;
    transform: translateY(-36px);
  }
  10% {
    opacity: 1;
    transform: translateY(-52px);
  }
  100% {
    opacity: 0;
    transform: translateY(-150px);
  }
}
@keyframes highlightCell {
  0% {
    z-index: 3;
  }
  100% {
    z-index: 3;
  }
}

.game {
  padding: 20px 0 60px;
  position: relative;
  &_start {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 64px;
    text-align: center;
    line-height: 72px;
    text-shadow: 1px 1px var(--primary-color-light);
  }
  &_board,
  &_highlight_container {
    margin: 0 auto;
    border-radius: 25px;
    max-width: calc(100vw - 30px);
    max-height: calc(100vw - 30px);
    &-size {
      &-4 {
        width: 404px;
      }
      &-5 {
        width: 504px;
      }
      &-6 {
        width: 604px;
      }
    }
  }
  &_highlight_wrapper {
    position: absolute;
    top: 0;
    width: 100%;
    pointer-events: none;
  }
  &_highlight_container {
    position: relative;
    overflow: hidden;
    &-size {
      &-4 {
        height: 404px;
      }
      &-5 {
        height: 504px;
      }
      &-6 {
        height: 604px;
      }
    }
  }
  &_highlight {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(
      90deg,
      transparent,
      hsla(0, 0%, 100%, 0.3),
      transparent
    );
    transform: translateX(-100%);
  }
  &_board {
    display: grid;
    grid-auto-rows: 1fr;
    border: 4px solid var(--border-color-light);
    margin: 0 auto;
    position: relative;
    opacity: 1;
    column-gap: 4px;
    row-gap: 4px;
    background-color: var(--border-color-light);
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    &_container {
      position: relative;
      &-isPlayer {
        .game_highlight {
          animation: highlightSwipe 1s;
        }
        .game_cell:not(.game_cell-isPlayer) {
          animation: highlightCell 1s;
        }
      }
    }
    &-size {
      &-4 {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(15.5vw - 2px);
            max-height: calc(25vw - 19px);
            &_left {
              max-height: calc(25vw - 19px);
            }
            &_right {
              max-height: calc(25vw - 19px);
            }
          }
          &_dice {
            max-height: calc(6.25vw - 1px);
            max-width: calc(6.25vw - 1px);
          }
          &:nth-child(4) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(4) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-left-radius: 21px;
            }
          }
        }
      }
      &-5 {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(10vw - 2px);
            max-height: calc(20vw - 16px);
            &_left {
              max-height: calc(20vw - 16px);
            }
            &_right {
              max-height: calc(20vw - 16px);
            }
          }
          &_dice {
            max-height: 5vw;
            max-width: 5vw;
          }
          &:nth-child(5) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(5) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
      &-6 {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(8.333333333333333vw - 2px);
            max-height: calc(16.66666vw - 16px);
            &_left {
              max-height: calc(16.66666vw - 16px);
            }
            &_right {
              max-height: calc(16.66666vw - 16px);
            }
          }
          &_dice {
            max-height: 4.166666666666666vw;
            max-width: 4.166666666666666vw;
          }
          &:nth-child(6) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(6) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
    }
    &:empty {
      opacity: 0;
      pointer-events: none;
    }
    &-busy {
      .game_cell {
        pointer-events: none;
      }
    }
  }
  &_cell {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 0;
    pointer-events: none;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    height: 100px;
    box-sizing: content-box;
    &:before {
      content: "";
      bottom: 0;
      left: 0;
      right: 0;
      top: 0;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      box-shadow: 0 0 0 2px var(--primary-color),
        0 0 4px 4px var(--primary-color);
      border-radius: 4px;
      z-index: 2;
      transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    }
    &:first-child {
      border-top-left-radius: 21px;
      &:before {
        border-top-left-radius: 21px;
      }
    }
    &:last-child {
      border-bottom-right-radius: 21px;
      &:before {
        border-bottom-right-radius: 21px;
      }
    }
    &-connect {
      &Top {
        border-top-width: 4px;
        margin-top: -4px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      &Right {
        border-right-width: 4px;
        margin-right: -4px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
      &Bottom {
        border-bottom-width: 4px;
        margin-bottom: -4px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
      &Left {
        border-left-width: 4px;
        margin-left: -4px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }
    &-corner {
      &TopLeft {
        border-top-left-radius: 7px;
      }
      &TopRight {
        border-top-right-radius: 7px;
      }
      &BottomRight {
        border-bottom-right-radius: 7px;
      }
      &BottomLeft {
        border-bottom-left-radius: 7px;
      }
    }
    &_corner {
      display: block;
      position: absolute;
      height: 4px;
      width: 4px;
      opacity: 0;
      overflow: hidden;
      z-index: 1;
      &:before {
        content: "";
        position: absolute;
        border-radius: 4px;
        height: 8px;
        width: 8px;
        background-color: var(--border-color-light);
      }
      &-top-left {
        top: -4px;
        left: -4px;
        .game_cell-connectTopLeft & {
          opacity: 1;
        }
      }
      &-top-right {
        top: -4px;
        right: -4px;
        &:before {
          left: -4px;
        }
        .game_cell-connectTopRight & {
          opacity: 1;
        }
      }
      &-bottom-right {
        bottom: -4px;
        right: -4px;
        &:before {
          left: -4px;
          top: -4px;
        }
        .game_cell-connectBottomRight & {
          opacity: 1;
        }
      }
      &-bottom-left {
        bottom: -4px;
        left: -4px;
        &:before {
          top: -4px;
        }
        .game_cell-connectBottomLeft & {
          opacity: 1;
        }
      }
    }
    &-selected {
      background-color: var(--text-color-dark) !important;
      color: #fff !important;
      &:before {
        opacity: 1;
      }
      &.game_cell-defender {
        &:before {
          box-shadow: 0 0 0 2px var(--error-color),
            0 0 4px 4px var(--error-color);
        }
      }
    }
    &_defender {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 9;
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
      &-active{
        opacity: 1;
      }
      &:after {
        content: "";
        position: absolute;
        width: 50px;
        height: 50px;
        background-size: contain;
      }
      &-top {
        &:after {
          bottom: -25px;
          background-image: url("../assets/images/attack-top.svg");
        }
      }
      &-right {
        &:after {
          left: -25px;
          background-image: url("../assets/images/attack-right.svg");
        }
      }
      &-bottom {
        &:after {
          top: -25px;
          background-image: url("../assets/images/attack-bottom.svg");
        }
      }
      &-left {
        &:after {
          right: -25px;
          background-image: url("../assets/images/attack-left.svg");
        }
      }
    }
    &-selectable {
      pointer-events: all;
      cursor: pointer;
    }
    &_dices {
      display: flex;
      flex-flow: column wrap;
      width: 42px;
      height: 84px;
      align-items: center;
      justify-content: flex-end;
      position: absolute;
      &_left {
        height: 84px;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      &_right {
        height: 84px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }
    }
    &_dice {
      background-image: url("../assets/images/dice-5.svg");
      height: 25px;
      width: 25px;
      margin: -2px;
      background-size: contain;
    }
    &_chain {
      position: absolute;
      font-size: 24px;
      font-weight: bold;
      z-index: 2;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 2px solid var(--primary-color);
      color: var(--primary-color);
      border-radius: 50%;
      text-shadow: 1px 1px #fff;
      opacity: 0;
      transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
      &-active {
        opacity: 1;
      }
      .game_cell-selected & {
        border-color: #fff;
        color: #fff;
        text-shadow: 1px 1px var(--primary-color);
        //color: #fff;
      }
    }
    &_add {
      position: absolute;
      font-size: 36px;
      line-height: 36px;
      color: var(--border-color-light);
      font-weight: 700;
      -webkit-text-stroke: 1px var(--warning-color);
      z-index: 9;
      top: 0;
      pointer-events: none;
      .game_cell-add & {
        animation: add 1.4s ease-out forwards;
      }
    }
  }
  @each $i, $color in $colors {
    &_cell {
      &-color {
        &-#{$i} {
          background-color: var(--player-color-#{$i});
          border-color: var(--player-color-#{$i}-dark);
          &:before {
            box-shadow: 0 0 0 2px var(--player-color-#{$i}),
              0 0 4px 4px var(--primary-color);
          }
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .game {
    &_board,
    &_highlight_container {
      width: 404px;
    }
    &_highlight_container {
      height: 404px;
    }
    &_board {
      &-size-4 {
        .game_cell {
          height: 91px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 72px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 61.33px;
        }
      }
    }
  }
}
@media (max-width: 410px) {
  .game {
    &_board,
    &_highlight_container {
      width: 335px;
    }
    &_highlight_container {
      height: 335px;
    }
    &_board {
      &-size-4 {
        .game_cell {
          height: 78.75px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 64.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 53.16px;
        }
      }
    }
  }
}
@media (max-width: 374px) {
  .game {
    &_board,
    &_highlight_container {
      width: 330px;
    }
    &_highlight_container {
      height: 330px;
    }
    &_board {
      &-size-4 {
        .game_cell {
          height: 77.5px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 63.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 50.33px;
        }
      }
    }
  }
}
@media (max-width: 359px) {
  .game {
    &_board,
    &_highlight_container {
      width: 290px;
    }
    &_highlight_container {
      height: 290px;
    }
    &_board {
      &-size-4 {
        .game_cell {
          height: 67.5px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 53.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 43.66px;
        }
      }
    }
  }
}
</style>
