import store from '@/store'
import { gainProbabilities, probabilities } from '../globals/globals'

export default class AiService {
  aiMove (i) {
    const player = store.getters['game/getPlayer']
    const board = store.getters['game/getBoard']
    const settings = store.getters['game/getSettings']
    // get all cells which can attack
    const cells = board.filter(cell => cell.owner === player.id && cell.dice > 1)
    // check if there are enough dices left to completely fill a cell
    const addResult = -(player.cells * 8) + player.total + player.size + player.reserve
    // set probability according to player aggressiveness
    let probabilityLimit = 0.52 - (0.12 * player.aggresive)
    // diminish aggresiveness after plays
    if (settings.mode > 1) {
      probabilityLimit += Math.min(i, 5) * (0.2 - player.aggresive * 0.08)
    }
    // set a more agressive stance if reserve is enough or a player is big enough
    if (addResult >= -1 || player.size > settings.size ** 1.4) {
      probabilityLimit = Math.min(probabilityLimit, 0.44)
    }
    let moves = []
    // stop if no moves can be made
    if (cells.length === 0) {
      return false
    }
    // loop through all cells which can attack
    cells.forEach(cell => {
      // loop through all adjacent cells
      cell.adjacents.forEach(adjId => {
        const adj = board[adjId]
        // check if another player owns adjacent cell
        if (adj.owner !== player.id) {
          // get the win probablity
          const probability = probabilities[cell.dice - 2][adj.dice - 1]
          // get the gain probablity
          const gainProbability = gainProbabilities[cell.dice - 2][adj.dice - 1]
          // check if player is aggressive enough to make this move
          if (probability > probabilityLimit) {
            const move = { attacker: cell.id, defender: adjId, owner: adj.owner, probability, gainProbability, weighedBenefit: 1 }
            moves.push(move)
          }
        }
      })
    })
    if (settings.mode > 1) {
      // check if player is too big and only attack them
      moves = this.attackBiggest(moves)
      // simulate all moves
      moves = this.simulateMoves(moves)
    }
    if (moves.length === 0) {
      return false
    }
    moves.sort((a, b) => (a.gainProbability < b.gainProbability) ? 1 : -1)
    moves.sort((a, b) => (a.weighedBenefit < b.weighedBenefit) ? 1 : -1)
    return moves[0]
  }

  // Attack player with half or more of the cells
  attackBiggest (moves) {
    const player = store.getters['game/getPlayer']
    const players = store.getters['game/getPlayers']
    const settings = store.getters['game/getSettings']
    // set the size from which a player is considered too big
    const limit = settings.size ** 2 / 2 + Math.round(player.aggresive * -2 + 1)
    // retrieve too big players
    const bigPlayer = players.filter(opponent => {
      return opponent.id !== player.id && opponent.size >= limit
    })
    if (bigPlayer.length) {
      // only get moves attacking too big player
      return moves.filter(move => {
        return move.owner === bigPlayer[0].id
      })
    }
    return moves.map(move => {
      const size = players.filter(opponent => opponent.id === move.owner)[0].size
      move.weighedBenefit = 0.8 + (settings.size ** 2 / 2) / size * 0.4
      return move
    })
  }

  simulateMoves (moves) {
    const player = store.getters['game/getPlayer']
    const board = store.getters['game/getBoard']
    // loop through all moves and simulate win
    moves.forEach(move => {
      const boardCopy = JSON.parse(JSON.stringify(board))
      boardCopy[move.defender].dice = boardCopy[move.attacker].dice - 1
      boardCopy[move.defender].owner = player.id
      boardCopy[move.attacker].dice = 1
      // set how much will be gained
      move.gain = this.calculateMoveResult(boardCopy, player.id)
      // set how much the defender will lose
      move.lose = this.calculateMoveResult(boardCopy, move.owner) * -1
      // set the benefit of gain vs lose
      move.benefit = (move.gain * (player.aggresive * -2 + 2.5)) + move.lose
      // set the weighed benefit according to probablity
      move.weighedBenefit *= move.benefit * (move.probability + 2)
    })
    return moves
  }

  calculateMoveResult (board, id) {
    const players = store.getters['game/getPlayers']
    const cellIds = []
    board.forEach((cell, i) => {
      if (cell.owner === id) {
        cellIds.push(i)
      }
    })
    let toAdd = 1
    for (let i = 0; i < cellIds.length; i++) {
      if (cellIds[i] > -1) {
        const group = []
        group.push(cellIds[i])
        delete cellIds[i]
        for (let x = 0; x < group.length; x++) {
          board[group[x]].adjacents.forEach(adj => {
            if (cellIds.includes(adj)) {
              group.push(adj)
              const index = cellIds.indexOf(adj)
              delete cellIds[index]
            }
          })
        }
        toAdd = Math.max(group.length, toAdd)
      }
    }
    const player = players[players.findIndex(x => x.id === id)]
    if (player) {
      return toAdd - player.size
    }
    return -1
  }
}
